#root {
    margin: 0;
}

* {
    margin: 0;
}

html,
body {
    margin: 0;
    height: 100%;
    padding: 0px;
    font-family: "Open Sans", sans-serif;
}

/* Home Things */

/* Header */

.homeHeader {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    height: 50vh;
    background-color: #a02813 !important;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/directorio-de-negocios-a257b.appspot.com/o/header.jpg?alt=media&token=b7220225-42c1-4bd5-8ea7-30c4ea36f578");
    background-blend-mode: multiply;
    background-size: cover;
}

@media only screen and (max-width: 500px) {
    #logo {
        width: 90%;
        position: relative;
        left: 5%;
        margin-bottom: 15px;
    }
    .aboutPasta-content {
        flex-direction: column;
    }
}

@media only screen and (max-height: 675px) {
    .homeHeader {
        height: 100vh;
    }
}

#catalogue-button {
    box-shadow: inset 0px 1px 0px 0px #ffd500;
    background: linear-gradient(to bottom, #ffd724 50%, #ffc926 100%);
    background-color: #ffd724;
    border-radius: 6px;
    border: 3px solid #ffffff;
    display: inline-block;
    cursor: pointer;
    color: #030000;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-size: 20px;
    font-weight: bold;
    padding: 15px 30px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #ffffff;
    width: 250px;
}

#catalogue-button:active {
    position: relative;
    top: 1px;
}

/* Recipes */
.catalogue-container,
.topRecipes-container {
    background-color: #fbfbff;
    font-family: "Lora", serif;
}
.catalogue-container h1,
.aboutPasta-container h1,
.topRecipes-container h1 {
    text-align: center;
    font-weight: 900;
    margin-top: 0px;
    padding-top: 25px;
}

#subtitle {
    margin-top: 0px;
}

.topRecipes {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.single-topRecipe {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    width: 200px;
    margin: 20px 0;
    border-radius: 5px;
    background-color: rgba(230, 230, 230, 1);
    padding: 15px;
}

.topRecipe-img {
    width: 150px;
    border-style: solid;
    border-radius: 50%;
    border-width: 10px;
    border-color: maroon;
}

.single-topRecipe p {
    font-size: 30px;
    font-weight: bold;
    margin-left: auto;
    margin-right: auto;
}

.single-topRecipe-img {
    text-align: center;
}

#single-recipe-button {
    background-color: burlywood;
    font-size: 15px;
    letter-spacing: 2px;
    font-weight: bolder;
    border-radius: 3px;
    padding: 15px 15px;
    border: none;
}

/*About Pasta */

.aboutPasta-container {
    background-color: silver;
    padding-bottom: 70px;
    font-family: "Lora", serif;
}

.aboutPasta {
    text-align: center;
}

.about-p {
    display: inline-block;
}

.facts {
    display: block;
    margin-bottom: 20px;
}

.aboutPasta-content {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    background-color: whitesmoke;
    padding: 30px 20px;
    border-radius: 10px;
    max-width: 60%;
    align-items: center;
}

.fas {
    font-size: 100px;
    margin: 20px;
}

.icon {
    display: inline-block;
    min-width: 150px;
}

#chef {
    position: absolute;
    transform: scaleX(-1);
    right: 0px;
    width: auto;
}

/* Single Recipe */

.recipes-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.singleRecipe-card {
    width: 200px;
    height: auto;
    margin: 15px;
    text-align: center;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    background-color: rgba(178, 34, 34, 1);
    padding: 3px;
}

.singleRecipe-card div {
    margin: 0px;
}

.card-img {
    width: 200px;
    height: 150px;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/directorio-de-negocios-a257b.appspot.com/o/pasticcio.png?alt=media&token=d60110fb-0ece-43fb-945a-27108cfd26be");
    background-size: cover;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

#single-recipe-btn {
    background-color: burlywood;
    font-size: 15px;
    letter-spacing: 2px;
    font-weight: bolder;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    padding: 15px 15px;
    border: none;
    width: 100%;
}
